  background-color: #eae7e734;
}

.custom-table tbody tr:nth-child(odd) {
  background-color: #fefefe;
}

.custom-table tbody tr:hover {
  background-color: #bdb9b9;
}

.modal {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
  z-index: 1050;
}

.modal .modal-dialog {
  margin: 0;
  width: auto;
  max-width: 80%;
}

.modal .modal-content {
  border-radius: 0;
}

.modal-dialog {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: auto;
  margin: 0.5rem;
  pointer-events: none;
  max-width: 500px;
  /* Adjust the maximum width as needed */
}

.modal-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0;
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  background-color: #f8f9fc;
}

.modal-title {
  margin-bottom: 0;
  line-height: 1.5;
  font-weight: 500;
  font-size: 1.2rem;
  /* Adjust the font size as desired */
}

.modal-body {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 1rem;
}

.profile-container {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.profile-image-container {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-right: 15px;
}

.student-details p {
  margin-bottom: 0.5rem;
}

.student-details {
  margin-top: 10px;
}

.modal-footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0.75rem;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  background-color: #f8f9fc;
}

.close:hover i {
  color: #ff0000;
}